* {
  padding: 0;
}

html {
  background-color: rgba($color: #98c1d9, $alpha: 1);
}

a {
  text-decoration: none;
  color: black;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.header-g {
  background-color: #3d5a80;
  border-bottom: 1px;
  border-bottom-left-radius: 1px;
  border-bottom-right-radius: 1px;
  font-family: "Roboto";
  font-size: 50px;
}

.footer-g {
  background-color: rgba($color: #293241, $alpha: 0.6);
}
input[type="submit"] {
  background-color: rgba($color: gray, $alpha: 0.5);
  border: none;
  border-radius: 4px;
}

input[type="number"] {
  border: none;
  background: transparent;
  padding-left: 15px;
  border: none;
  -moz-appearance: textfield;
}

input[type="submit"] {
  margin-top: 10px;
}
input[type="text"] {
  font-size: 18px;
  margin-left: 10px;
  background: transparent;
  border: none;
  border-bottom: 1px solid #000000;
  width: 90%;
}

.container-grid {
  background-color: rgba($color: #98c1d9, $alpha: 0.6);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 0.3fr 4fr 0.25fr;
  gap: 15px;
  width: 100vw;
  height: 100vh;
}

.header-g {
  grid-column: 1 / 3;
  text-align: center;
  font-family: 'Roboto Condensed', sans-serif;
}

.generalinfo-g {
  font-family: "Roboto";
  grid-column: 1 / 2;
  border: none;
  border-right: 3px solid gray;
  border-radius: 2px;
}

.generalinfo-g h2 {
  text-align: center;
}

.curriculum-g {
  border-radius: 4px;
  background-color: rgb(238, 238, 238);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  grid-column: 2 / 3;
}

.footer-g {
  font-size: 24px;
  text-align: center;
  padding-top: 10px;
  grid-column: 1 / 3;
}

form {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  margin: 10px;
}

.field {
  flex-grow: 1;
}

.g-div {
  display: flex;
  flex-grow: 1;
  background-color: rgba($color: gray, $alpha: 0.2);
  border-radius: 20px;
}

.year-inputs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.box {
  background-color: rgba($color: white, $alpha: 0.2);
  border-radius: 20px;
}

.content-box {
  padding: 2px;
  margin: 10px;
}

.visible-button {
  width: 50px;
  height: 25px;
  margin-left: 92%;
  font-size: 22px;
  background-color: rgba(128, 128, 128, 0.5);
  border: none;
  border-radius: 4px;
}

.p-curriculum {
  margin: 15px;
  font-size: 26px;
  font-family: 'Roboto condensed';
}
